.hero {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2.5rem;
    max-width: 68.75rem;
    margin: auto;
    padding-top: 2rem;
  }

  .hero-skills{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2.5rem;
    max-width: 55vw;
    margin-left: 14vw;
    padding-bottom: 2rem;
    margin-top: 1rem;
  }
  
.hero img {
  height: 20rem;
  width: 20rem;
}

.bio {
  width: 25rem;
  padding: 0.625rem;
  border-radius: 6px;
}

.bio h1 {
    color: white;
    margin-bottom: var(--bottom-margin);
}

.bio p {
  color: white;
  line-height: var(--line-height);
  padding: 0.3rem 0;
  font-size: 2rem;
}

.circle-label h1{
  font-size: 3rem;
  width: 4rem;
  margin-right: 7rem;
}

.skills-container{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2.5rem;
  max-width: 68.75rem;
  margin: auto;
  padding-top: 2rem;
}


@media only screen and (max-width: 2000px) {
  .hero {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2.5rem;
    max-width: 100vh;
    margin-left: 22vh;
    padding-top: 1rem;
  }
  
.hero img {
  height: 25vh;
  width: 25vh;
}

.hero-skills{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2.5rem;
  max-width: 60vw;
  margin-left: 13vw;
  padding-bottom: 2rem;
  margin-top: 1rem;
}


.bio p {
  color: white;
  line-height: var(--line-height);
  padding: 0.5rem 0;
  font-size: 2rem;
}
}

@media only screen and (max-width: 1400px) {
  .hero {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    max-width: 80vh;
    margin-left: 30vh;
    padding-top: 1rem;
  }

  .hero-skills{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2.5rem;
    max-width: 60vw;
    margin-left: 10vw;
    padding-bottom: 2rem;
    margin-top: 2rem;
  }
  .bio h1 {
    color: white;
    margin-bottom: 1rem;
}

.circle{
  width: 10vw;
  height: 10vh;
}

.circle-label h1{
  font-size: 2rem;
}
  
.hero img {
  height: 25vh;
  width: 25vh;
}

.bio {
  width: 25rem;
  padding: 0.625rem;
}

.bio h1 {
    color: white;
    margin-bottom: var(--bottom-margin);
}

.bio p {
  color: white;
  line-height: var(--line-height);
  padding: 0.3rem 0;
  font-size: 1.5rem;
}


}

@media only screen and (max-width: 960px) {
  .hero{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    max-width: 80vh;
    margin-left: 25vh;
    padding-top: 1rem;
  }

  .hero img {
    display: flex;
    flex-direction: column;
    position: relative;
    top: -2rem;
    left: 3rem;
    height: 25vh;
    width: 25vh;
  }

  .bio {
    display: block;
    flex-direction: column;
    position: relative;
    width: 15rem;
    top: 12.5rem;
    left: -12.5rem;
    padding: 0rem;
    margin-right: 0vw;
  }

  .bio-text{
    width: 15rem;
  }
  
  .bio p {
    color: white;
    line-height: var(--line-height);
    padding: 0.5rem 0;
    font-size: 1.5rem;
  }

  .hero-skills{
    display: none;
    align-items: center;
    justify-content: center;
    gap: 2.5rem;
    max-width: 100vh;
    margin-left: 0vh;
    padding-top: 1rem;
  }
}




