.twitter-container{
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 1%;
    margin-left: 75%;
    margin-top: -2.5rem;
}



.twitter-container h1{
    font-size: 40px;
}

.gallery{
    display: flex;
    flex-direction: row;
    position: relative;
    float: left;
    width: 75%;
    height: 100%;
}

.gallery img{
    height: 18rem;
    width: 27rem;
    padding: 2%;
    margin-top: 3%;
    
}


.gallery img:hover{
    transition: transform .4s;
    transform: scale(1.2);
}

.gallery-container h1{
    font-size: 40px;
    margin-left: -25%;

}



@media only screen and (min-width: 300px) {

    .gallery-container{
        width: auto;
        height: auto;
    }

    .twitter-container{
        display: flex;
        flex-direction: column;
        position: relative;
        top: 25rem;
        left: -10rem;
        width: 20vw !important;
        height: 20vh !important;
    }

    #twitter-widget-0{
        width: 10;
    }


    .gallery img{
        height: 16vh;
        width: 27vw;
        padding: 2%;
        margin-top: 3%;
        
    }

    .gallery-container{
        margin-left: -5%;
    }

    .gallery-container h1{
        font-size: 40px;
        margin-left: 0%;
    
    }
}

@media only screen and (min-width: 1100px) {
    .twitter-container{
        display: flex;
        flex-direction: column;
        position: relative;
        top: 0rem;
        left: 12rem;
        width: 25vw !important;
        height: 25vh !important;
        margin-left: 25vw !important;
    }

    .gallery{
        display: flex;
        flex-direction: row;
        position: relative;
        left: 5rem;
        float: left;
        width: 60%;
        height: 100%;
    }

    .gallery img{
        height: 23vh;
        width: 21vw;
        padding: 2%;
        margin-top: 3%;
        
    }
}

@media only screen and (min-width: 1700px) {
    .twitter-container{
        display: flex;
        flex-direction: column;
        position: relative;
        margin: 0%;
        top: 0rem;
        left: 12rem;
        margin-right: 0rem;
        margin-top: -2.5rem;
        width: 25vw !important;
        height: 25vh !important;
        margin-left: 25vw !important;
    }

    .gallery{
        display: flex;
        flex-direction: row;
        position: relative;
        left: 5rem;
        float: left;
        width: 60%;
        height: 100%;
        margin-right: 3rem;
        margin-left: 3rem;
    }

    .gallery img{
        height: 24vh;
        width: 22vw;
        padding: 2%;
        margin-top: 3%;
        
    }
}

@media only screen and (min-width: 2000px){
    .gallery img{
        height: 26vh;
        width: 22vw;
        padding: 2%;
        margin-top: 3%;
        
    }
}