.connect{
    display: flex;
    flex-direction: row;
    position: relative;  
    margin: 1%;
    margin-left: 45%;
}

.container h1{
    font-size: 40px;
}

.connect i{
    margin-left: 2%;
}

.connect a{
    color: white;
    margin-left: 2%;
}

@media only screen and (max-width: 1400px) {

    .connect{
        display: flex;
        flex-direction: row;
        position: relative;  
        margin: 1%;
        margin-left: 41vw;
    }
    
    .connect i{
        margin-left: 5%;
    }
}

@media only screen and (max-width: 960px) {

    .connect{
        display: flex;
        flex-direction: row;
        position: relative;  
        margin: 1%;
        margin-left: 18vw;
    }
    
    .connect i{
        margin-left: 10%;
    }
}
