.NavbarItems {
    background: linear-gradient(90deg, rgb(0, 0, 0) 0%, rgb(214, 214, 214) 80%);
    opacity: 0.8;
    height: 100px;
    display: flex;
    position: fixed;
    top: 0;
    width: 100%;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    z-index: 500
}

.navbar-logo{
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
}

.fa-react{
    margin-left: 0.5rem;
    font-size: 1.6rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-links {
    color: rgb(0, 0, 0);
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover{
    background-color: rgb(255, 255, 255);
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.fa-bars {
    color: rgb(0, 0, 0);
}


.nav-links-mobile{
    display: none;
}

.menu-icon{
    display: none;
}

@media only screen and (max-width: 1940px){
    .NavbarItems {
        background: linear-gradient(90deg, rgb(0, 0, 0) 0%, rgb(214, 214, 214) 80%);
        opacity: 0.8;
        height: 100px;
        display: flex;
        position: fixed;
        top: 0;
        width: 100%;
        justify-content: center;
        align-items: center;
        font-size: 1.2rem;
        z-index: 500
    }
}

@media screen and (max-width: 960px){
    .NavbarItems {
        position: fixed;
        width: 100vw;
    }

    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active{
        background: linear-gradient(90deg, rgb(0, 0, 0) 0%, rgb(214, 214, 214) 80%);
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links{
        text-align: center;
        padding: 2rem;
        width: 90%;
        display: table;
    }

    .nav-links:hover{
        background-color: #ffffff;
        border-radius: 0;
    }

    .navbar-logo{
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
    }

    .menu-icon{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size:  1.8rem;
        cursor: pointer;
    }

    .fa-times{
        color: rgb(0, 0, 0);
        font-size: 2rem;
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        background: #6b6e6e;
        text-decoration: none;
        color: #fff;
        font-size: 1.5rem;
    }

    .nav-links-mobile:hover{
        background: #fff;
        color: #6b6b6b;
        transition: 250ms;
    }


    Button {
        display: none;
    }
}
